import { Button, Stack, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import DeleteButton from 'components/Button/Delete'
import EditButton from 'components/Button/Edit'
import { Fragment, useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getTemplates } from 'redux/slices/CMS/Template'
import { getThemes } from 'redux/slices/CMS/Theme'

export default function Theme() {
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const location = useLocation()
	const status = useAppSelector((s) => s.CMS.Theme.status)
	const list = useAppSelector((s) => s.CMS.Theme.list)
	const dispatch = useAppDispatch()
	const currentPath = useAppSelector((s) => s.UI.currentPath)
	useEffect(() => {
		if (status === 'init') dispatch(getThemes())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	const templateStatus = useAppSelector((s) => s.CMS.Template.status)
	const templates = useAppSelector((s) => s.CMS.Template.list)
	useEffect(() => {
		if (templateStatus === 'init') dispatch(getTemplates())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	if (!tokenInfo) return null
	return (
		<Stack gap={1}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					テーマ一覧
				</Typography>
				<Link to="/cms/theme/create" state={{ location }}>
					<Button
						variant="outlined"
						sx={{
							minWidth: 120,
						}}
					>
						新規作成
					</Button>
				</Link>
			</Stack>
			<DataGrid
				rows={list}
				columns={[
					{
						field: 'name',
						headerName: 'テーマ名',
						flex: 1,
					},
					{
						field: 'action',
						headerName: '動作',
						width: 140,
						renderCell: (v) => {
							return (
								<Fragment key={v.id}>
									<Link to={`/cms/theme/${v.row.id}`}>
										<EditButton />
									</Link>
									<Link to={`/cms/theme/${v.row.id}/delete`} state={{ location }}>
										<DeleteButton />
									</Link>
								</Fragment>
							)
						},
					},
					{
						field: 'status',
						headerName: '編集者',
						flex: 1,
						renderCell: (v) => {
							const editor = currentPath.find((f) => f.path === location.pathname + '/' + v.row.id)

							return (
								<Typography variant="caption" mr={1}>
									{editor?.auth.email}
								</Typography>
							)
						},
					},
					{
						field: 'used',
						headerName: '使用場所(テンプレート)',
						flex: 2,
						renderCell: (v) => {
							return (
								<Stack direction="row" gap={1} key={v.id}>
									{templates
										.filter((f) => f.themeId === v.row.id && f.active)
										.map((tem) => {
											return (
												<Fragment key={tem.id}>
													<Link to={`/cms/template/${tem.id}`}>{tem.name}</Link>
												</Fragment>
											)
										})}
								</Stack>
							)
						},
					},
				]}
				initialState={{
					pagination: {
						paginationModel: { page: 0, pageSize: 10 },
					},
				}}
				pageSizeOptions={[10, 20, 30, 50]}
				sx={{
					'& .MuiDataGrid-cell:focus': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:focus-within': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:hover': {},
					'& .MuiDataGrid-columnHeaders': {
						bgcolor: 'primary.dark',
						color: '#fff',
					},
					'& .MuiDataGrid-columnHeaders svg': {
						color: '#fff',
					},
				}}
				density="compact"
				hideFooterSelectedRowCount
				editMode="row"
				disableColumnMenu
				disableRowSelectionOnClick
			/>
		</Stack>
	)
}
